import React from 'react';
import marked from 'marked';
import styles from './styles.js';
import './markdownStyles.css';

class Previewer extends React.Component {
  parseText = (text) => {
    var inputText = text;
    var lines = inputText.split('\n');
    var parsedText = '';
    for(var i = 0;i < lines.length;i++){
      parsedText = parsedText + lines[i] + ' \n';
    }
    return (parsedText);
  }

  getMarkdownText(text) {
    var rawMarkup = marked(text, {sanitize: true});
    return { __html: rawMarkup };
  }
  render() {
    return (
      <div id="preview-container" style = {styles.windowContainer}>
        <div id="preview-title" style = {styles.windowTitle}>Previewer</div>
        <div id="preview" style = {styles.previewWindow} dangerouslySetInnerHTML={this.getMarkdownText(this.parseText(this.props.text))} />
      </div>
    );
  }
}

export default Previewer



