const text = ' \
# This is a heading \n \
\n \
## This is a sub-heading... \n \
### And here is some other cool stuff: \n \
\n \
Code goes between 2 backticks: `<div></div>` \n \
\n \
Multi-line code requires 3 backticks: \n \
```` \n \
// this is multi-line code: \n \
Line 1\n \
Line 2\n \
```` \n \
\n \
Text can be **bold**, or _italic_, or **_both!_** \n \
You can also ~~cross things out~~. \n \
\n \
You can define [links](https://www.freecodecamp.com), and use \n \
> Block Quotes! \n \
\n \
Even tables can be created: \n \
\n \
Header 1 | Header 2 | Header 3 \n \
------------ | ------------- | -------------  \n \
cell | cell | cell \n \
cell | cell | cell \n \
\n \
There are lists: \n \
  - Some are bulleted. \n \
     - With different indentation levels. \n \
\n \
And there are numbererd lists too:\n \
1. Item \n \
2. Item \n \
\n \
And of course you can embed images: \n \
![React Logo w/ Text](https://goo.gl/Umyytc) \n \
';

const defaultText = () => {
  return text;
}

export default defaultText;



