import React from 'react';
import styles from './styles.js';

const Editor = ({text, onInput}) => {

  return (
    <div id="previewContainer" style={styles.windowContainer}>
      <div id="previewTitle" style={styles.windowTitle}>Editor</div>
      <textarea
        id='editor'
        style={styles.inputWindow}
        defaultValue={text}
        onInput={onInput}
      />
  </div>
  );
}

export default Editor;



